import React from "react";
import Styled from "styled-components";
import List from "./List";
import Popup from "components/common/Popup";
import emptyFeedback from 'assets/empty-feedback.png';

const Feedback = Styled(List.Item)``;

Feedback.Name = Styled.p`
    color: #4A4A4A;
    font-size: 0.4267rem;
`;

Feedback.Date = Styled.p`
    margin-top: 0.1334rem;
    color: #B8B5B5;
    font-size: 0.32rem;
`;

Feedback.Number = Styled.p`
    color: #4A4A4A;
    font-size: 0.32rem;
`;

Feedback.Status = Styled.p`
    margin-top: 0.16rem;
    color: #B8B5B5;
    font-size: 0.32rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    &.R {
        color: #4EA0FF;
    }

    img {
        height: 0.4267rem;
        margin-left: 0.16rem;
        display: block;
    }
`;

Feedback.Content = Styled(Popup.Content)`
  padding: 0.64rem;
  color: #4EA0FF;
  font-size: 0.3734rem;
  line-height: 0.6934rem;
  overflow-y: auto;
  justify-content: flex-start;

  .Fx {
    display: flex;
  }

  .Up {
    margin-top: 0.1rem;
    flex-wrap: wrap;

    & > div {
      width: 1.7067rem;
      height: 1.7067rem;
      margin-right: 0.4rem;
      margin-bottom: 0.3rem;
      border: 1px solid #F5F5F5;
      border-radius: 2px;

      img {
        width: 100%;
        height: 100%;
        border-radius: 2px;
        display: block;
      }
    }
  }
`;

Feedback.Label = Styled.span`
  color: #4A4A4A;
  white-space: nowrap;
`;

const Steps = Styled(Popup.Content)`
    justify-content: space-between;

    ul {
        margin-top: -0.08rem;
        font-size: 0.4rem;

        li {
            margin-bottom: 0.2134rem;
            color: #4EA0FF;

            span {
                margin-left: 0.0534rem;
                color: #4A4A4A;
            }
        }
    }

    button {
        color: #FFFFFF;
        font-size: 0.3734rem;
        line-height: 0.96rem;
        background-color: #4EA0FF;
        border: none;
        border-radius: 8px;
    }
`;

export default class extends React.PureComponent {
  state = {
    showSteps: false
  };
  render() {
    const {
      showSteps
    } = this.state;
    return <>
      <List.Container
        style={{ overflowY: showSteps ? "hidden" : "auto" }}
      >
        <List.Empty>
          <img
            src={emptyFeedback}
            alt="empty-feedback"
            onClick={() => false}
          />
        </List.Empty>
        <List.Bottom>
          <span
            onClick={() => {
              this.setState({ showSteps: true });
            }}
          >
            如何申请
        </span>
        </List.Bottom>
      </List.Container>
      <Popup style={{ display: showSteps ? "block" : "none" }}>
        <Popup.Dialog style={{ display: showSteps ? "flex" : "none" }}>
          <Popup.CloseButton
            onClick={() => {
              this.setState({ showSteps: false });
            }}
          />
          <Steps>
            <Popup.Content.Title>申请步骤</Popup.Content.Title>
            <ul>
              <li>
                1.<span>首页点击查看已有报告</span>
              </li>
              <li>
                2.<span>我的报告</span>
              </li>
              <li>
                3.<span>点击报告</span>
              </li>
              <li>
                4.<span>异议申请/滑动至报告底部</span>
              </li>
              <li>
                5.<span>点击“对结果有异议”按钮</span>
              </li>
              <li>
                6.<span>输入详细情况</span>
              </li>
              <li>
                7.<span>等待审核回复</span>
              </li>
            </ul>
            <button
              onClick={() => {
                this.setState({ showSteps: false });
              }}
            >
              好的
          </button>
          </Steps>
        </Popup.Dialog>
      </Popup>
    </>
  }
}
