import React from "react";
import Styled from "styled-components";
import { Link } from "react-router-dom";
// import { Modal, Toast } from "antd-mobile";
import List from "./List";
import Popup from "components/common/Popup";
import verify from 'assets/verify.png';

const Report = Styled(List.Item)``;

Report.Name = Styled.p`
    color: #4A4A4A;
    font-size: 0.4267rem;
`;

Report.Date = Styled.p`
    margin-top: 0.1334rem;
    color: #B8B5B5;
    font-size: 0.32rem;
`;

Report.Status = Styled.span`
    color: #E78811;
    font-size: 0.3734rem;
    display: block;

    &.S {
      color: #55A300;
    }

    &.Or {
      color: #E73B11;
    }

    &.G {
      color: #B8B5B5;
    }
`;

Report.Dialog = Styled(Popup.Content)`
    justify-content: center;
    align-items: center;

    div {
      margin: 0.4rem 0 0.5334rem;
      color: #4A4A4A;
      font-size: 0.3734rem;
      line-height: 0.5334rem;
      text-align: center;
    }

    .Fdbk {
      width: 100%;
      bottom: 0.5334rem;
      left: 0;
      display: flex;
      justify-content: center;
      position: absolute;

      a {
        color: #4EA0FF;
        font-size: 0.3734rem;
        display: block;
      }
    }
    
    img {
      width: 4.2667rem;
      display: block;
    }

    button {
        width: 3.2rem;
        color: #FFFFFF;
        font-size: 0.3734rem;
        line-height: 0.96rem;
        background-color: #4EA0FF;
        border: none;
        border-radius: 8px;
    }
`;

export default class extends React.PureComponent {
  state = {
    showDialog: false,
    issueType: 0 // 1 = 人脸识别失败, 2 = 次数超限.
  };
  checkReport = () => {
    this.props.history.push(`/report`);
  };
  popup = status => {
    this.setState({
      showDialog: true,
      issueType: status
    });
  };
  reface = () => {
    const { issueType } = this.state;
    if (issueType === 1) {
      const { location, history } = this.props;
      history.push(
        `/progress/2?re=1${
        !!location.search ? location.search.replace("?", "&") : ""
        }`
      );
    } else {
      this.setState({
        showDialog: false
      });
    }
  };
  render() {
    const {
      showDialog,
      issueType
    } = this.state;
    return <>
      <List.Container style={{ overflowY: showDialog ? "hidden" : "auto" }}>
        <List>
          <Report onClick={() => this.popup(1)}>
            <div>
              <Report.Name>大数据信用报告</Report.Name>
              <Report.Date>提交时间：2019-07-09</Report.Date>
            </div>
            <Report.Status>身份识别未通过</Report.Status>
          </Report>
          <Report
            onClick={this.checkReport}
          >
            <div>
              <Report.Name>大数据信用报告</Report.Name>
              <Report.Date>失效时间：2058-05-08</Report.Date>
            </div>
            <Report.Status className="S">可查看</Report.Status>
          </Report>
          <Report onClick={() => this.popup(2)}>
            <div>
              <Report.Name>大数据信用报告</Report.Name>
              <Report.Date>提交时间：2019-07-09</Report.Date>
            </div>
            <Report.Status className="Or">今日次数超限</Report.Status>
          </Report>
          <Report>
            <div>
              <Report.Name>大数据信用报告</Report.Name>
              <Report.Date>提交时间：2019-07-09</Report.Date>
            </div>
            <Report.Status className="G">已退款</Report.Status>
          </Report>
        </List>
        <List.Bottom>
          <Link to="/progress">立即查询</Link>
        </List.Bottom>
      </List.Container>
      <Popup style={{ display: showDialog ? "block" : "none" }}>
        <Popup.Dialog>
          <Popup.CloseButton
            onClick={() => {
              this.setState({
                showDialog: false
              });
            }}
          />
          <Report.Dialog>
            <img
              src={verify}
              alt="verify"
              onClick={() => false}
            />
            <div>
              <p>
                {issueType === 1
                  ? "此报告身份验证未通过"
                  : "今日验证次数超限"}
              </p>
              <p>请{issueType === 1 ? "重新验证" : "明天再试"}</p>
            </div>
            <button
              onClick={() => {
                this.reface();
              }}
            >
              {issueType === 1 ? "重新验证" : "好的"}
            </button>
            <p className="Fdbk">
              <Link to={`/feedback`}>异议申请</Link>
            </p>
          </Report.Dialog>
        </Popup.Dialog>
      </Popup>
    </>
  }
}
