import React from "react";
import Styled from "styled-components";
import { Helmet } from "react-helmet";
import { Tabs } from "antd-mobile";
import Reports from "./Reports";
import Feedbacks from "./Feedbacks";

const MyPage = Styled.div`
    height: 100%;
    position: relative;
`;

const tabs = [
  { title: "我的报告", type: "reports" },
  { title: "异议申请记录", type: "feedbacks" }
];

export default class extends React.PureComponent {
  state = {
    name: "",
    phone: ""
  };
  componentDidUpdate() {
    const { match, history } = this.props;
    if (match.params.type !== "reports" && match.params.type !== "feedbacks") {
      history.replace("/my/reports");
    }
  }
  onTabChange = ({ type }) => {
    const { match, history } = this.props;
    if (match.params.type !== type) {
      history.push(`/my/${type}`);
    }
  };
  render() {
    const activeIndex = tabs.findIndex(
      e => e.type === this.props.match.params.type
    );
    return (
      <MyPage>
        <Helmet>
          <title>
            {this.props.match.params.type === "reports"
              ? "我的报告"
              : "异议申请记录"}
          </title>
        </Helmet>
        <Tabs
          tabs={tabs}
          initialPage={0}
          page={activeIndex}
          tabBarInactiveTextColor="#5B5B5B"
          tabBarActiveTextColor="#4EA0FF"
          tabBarUnderlineStyle={{
            width: "2.4rem",
            marginLeft: "calc(25% - 1.2rem)",
            border: "#4EA0FF 0.0267rem solid"
          }}
          animated={false}
          swipeable={false}
          onChange={t => this.onTabChange(t)}
        >
          <Reports {...this.props} />
          <Feedbacks {...this.props} />
        </Tabs>
      </MyPage>
    );
  }
}
