import Styled from 'styled-components';

const List = Styled.ul`
    width: 100%;
    min-height: calc(100% - 1.6rem);
    margin-bottom: 1.6rem;
`;

List.Container = Styled.div`
    height: 100%;
    position: relative;
`;

List.Item = Styled.li`
    padding: 0.2667rem 0.2667rem 0.3467rem;
    border-bottom: 1px solid #E2E3E6;
    display: flex;
    justify-content: space-between;
    align-items: center;
`;

List.Empty = Styled.div`
    width: 100%;
    height: 100%;

    img {
        width: 4.9334rem;
        top: 3.7334rem;
        left: 50%;
        margin-left: -2.4667rem;
        display: block;
        position: relative;
    }
`;

List.Bottom = Styled.div`
    width: 100%;
    height: 1.6rem;
    margin-top: -1.6rem;
    text-align: center;

    a, span {
        color: #4EA0FF;
        font-size: 0.3734rem;
        line-height: 1.6rem;
    }
`;

export default List;